@font-face {
  font-family: mariofont;
  src: url(./SuperMarioBrosNES.ttf);
}

.header {
  font-family: mariofont;
  src: url(./SuperMarioBrosNES.ttf);
  color: white;
  background-color: black;
  float: left;
  width: 100%;
  position: fixed;
  top: 0;
  max-height: 75px;
}

.nav--buttons {
  margin-top: 79px;
  background-color: black;
  color: white;
  font-family: "Oswald", sans-serif;
}

.console__name {
  font-family: mariofont;
  src: url(./SuperMarioBrosNES.ttf);
  padding-top: 13px;
  font-size: 2.13vh;
}

.game__yearreleased {
  font-family: "Lora", serif;
}

.game__title {
  font-family: "Oswald", sans-serif;
}

.mario {
  max-width: 45px;
  float: left;
  width: 100%;
  padding-top: 10px;
  padding-right: 10px;
  padding-left: 10px;
  padding-bottom: 20px;
  margin-right: 10px;
}

.modal__header {
  font-family: mariofont;
  src: url(./SuperMarioBrosNES.ttf);
}

.header__title {
  padding-left: 10px;
  font-size: 32px;
  padding-bottom: 15px;
}
@media screen and (max-width: 738px) {
  .header__title {
    font-size: 24px;
    padding-top: 10px;
  }
}
@media screen and (max-width: 560px) {
  .header__title {
    padding-top: px;
    padding-bottom: 5px;
  }
}

.mario:hover {
  background-color: #9290ff;
}

#app {
  margin-top: 80px;
}
.console {
  float: left;
  list-style: none;
  width: 100%;
}
.game {
  text-align: left;
  float: left;
  list-style: none;
  /* margin: 5px;
      padding-left: 5px;
      padding-right: 5px; */
  text-align: center;
  max-width: 150px;
  width: 100%;
  z-index: 5;

  transition: transform 1s;
}

.game__imagepath {
  max-height: 205px;
  max-width: 150px;
  width: 100%;
  object-fit: contain;
}

.game__infoimagepath {
  max-width: 150px;
  width: 100%;
}

.game:hover {
  transform: scale(1.15);
}
.game-trigger {
  font-size: 2.5vh;
  padding-left: 10px;
  padding-right: 10px;
  cursor: pointer;
}
.console-trigger {
  font-size: 2.5vh;
  padding-left: 10px;
  padding-right: 10px;
  cursor: pointer;
}
.company-trigger {
  font-size: 2.5vh;
  padding-left: 10px;
  padding-right: 10px;
  cursor: pointer;
}
.remove-game {
  font-size: 2.5vh;
  padding-left: 10px;
  padding-right: 10px;
  cursor: pointer;
}
.game-trigger:hover,
.console-trigger:hover,
.company-trigger:hover,
.remove-game:hover {
  background-color: red;
}

.modal-console {
  position: fixed;
  left: 0;
  top: 10px;
  width: 100%;
  height: 100%;
  max-width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  z-index: 3;
  visibility: hidden;
  transform: scale(1.1);
  transition: visibility 0s linear 0.25s, opacity 0.25s 0s, transform 0.25s;
}

.consolemodal-content {
  position: absolute;
  /* top: 50%;
        left: 50%; */
  top: 50%;
  left: 50%;
  width: 25%;
  height: 25%;
  font-size: 20px;

  transform: translate(-50%, -50%);
  background-color: white;
  padding: 1rem 1.5rem;
  width: 24rem;
  border-radius: 0.5rem;
  overflow: auto;
}

.close-button {
  float: right;
  width: 1.5rem;
  line-height: 1.5rem;
  text-align: center;
  cursor: pointer;
  border-radius: 0.25rem;
  background-color: lightgray;
}
.close-button:hover {
  background-color: darkgray;
}
.show-consolemodal {
  opacity: 1;
  visibility: visible;
  transform: scale(1);
  transition: visibility 0s linear 0s, opacity 0.25s 0s, transform 0.25s;
}

.modal-game {
  position: fixed;
  left: 0;
  top: 10px;
  width: 100%;
